import { useMemo } from "react";
import { Image } from "react-bootstrap";
import { IS_TAGEMBED } from "../../../../../../../actions/api";
import { baseUrl, FontAwesomeIcon } from "../../../../../../../Assets/AssetsHelper";
import { generateRandomArray } from "../../../../../../../utils";

const Preview = ({ title, subTitle, backgroundImage, selectedColor, btnText, logoImage, isReview, onsiteLanguage, formContent }) => {
    const content = useMemo(() => formContent?.[onsiteLanguage?.value || "en"], [onsiteLanguage, formContent]);

    return content ? <div className="f-center flex-wrap snapUpContainer" style={{ backgroundColor: selectedColor, backgroundImage: `url(${backgroundImage})` }}>
        <div className="onsite_preview_inner w-100">
            <div className="t_modal_wrap">
                <div className="t_modal_container">
                    <div className="t_modal_content">
                        {logoImage ? <div className="t_m_logo_w">
                            <img loading="lazy" src={logoImage} className="t_m_logo" width="400" height="50" alt="logo" />
                        </div> : null}
                        <div className="t_m_close_btn fa fa-xmark">
                            <div></div>
                        </div>
                        {title ? <div className="t_m_title">{title}</div> : null}
                        {subTitle ? <div className="t_m_subtitle">{subTitle}</div> : null}
                        {isReview ? <div className="t_f_group">
                            {/* <div className="t_label_ t_label_center">Rate the Product <div className="t_t_danger">*</div></div> */}
                            <div className="t_rate_wrap">
                                {generateRandomArray(5).map((number, index) => <div key={index} className={`t_star_ico_wrap ${number <= 4 ? 't_h_active_' : ''}`}>
                                    <div className="t_star_ico w-44px h-44px">
                                        <FontAwesomeIcon icon={'star'} />
                                    </div>
                                </div>)}
                            </div>
                        </div> : null}
                        {isReview ? <div className="t_f_group">
                            <div className="t_label_">
                                {content?.titleLabel ?? ""}
                                <div className="t_t_danger">*</div>
                            </div>
                            <input type="text" className="t_from_input " placeholder={content?.titlePh ?? ""} value="" />
                        </div> : null}
                        <div className="t_f_group position-relative">
                            <div className="t_label_">
                                {isReview ? content?.reviewLabel ?? "" : content?.captionLabel ?? ""}
                                <div className="t_t_danger">*</div>
                            </div>
                            <textarea className="t_from_textarea" placeholder={content?.reviewPh ?? ""} readOnly></textarea>
                            <div className="write-with-ai position-absolute">
                                <div className=""><Image src={baseUrl(`media/images/ai.svg`)} alt="ai" /></div>
                                <span className=""> {content?.writeWithAiBtn ?? ""}</span>
                            </div>
                        </div>
                        <div className="t_f_group">
                            {/* <div className="t_label_">Picture/Video (optional)</div> */}
                            <div className="t_f_upload">
                                <div className="t_m_upload t_m_dd">
                                    <div className="t_m_up_wrp">
                                        <div className="uppy-Container">
                                            <div className="uppy-Root" dir="ltr">
                                                <div className="uppy-u-reset">
                                                    <div className="uppy-DragDrop-inner">
                                                        <div className="uppy-DragDrop-label">{content?.uploadText ?? ""}</div>
                                                        <span className="uppy-DragDrop-note text-gray-800">{content?.contentSize ?? ""}</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="t_row_">
                            <div className="t_col_">
                                <div className="t_f_group">
                                    <div className="t_label_">
                                        {content?.nameLabel ?? ""}
                                        <div className="t_t_danger">*</div>
                                    </div>
                                    <input type="text" className="t_from_input " placeholder={content?.namePh ?? ""} value="" />
                                </div>
                            </div>
                            <div className="t_col_">
                                <div className="t_f_group">
                                    <div className="t_label_">
                                        {content?.emailLabel ?? ""}
                                        <div className="t_t_danger">*</div>
                                    </div>
                                    <input type="text" className="t_from_input " placeholder={content?.emailPh ?? ""} value="" />
                                </div>
                            </div>
                        </div>
                        <div className="t_f_group">
                            <div className="t_f_check_inline"><input type="checkbox" id="t_accept" className="t_from_check" value="accept" checked="" /><label className="t_check_label_" for="t_accept">{content?.termsText ?? ""}<a href={IS_TAGEMBED ? "https://tagembed.com/review-hub-content-rights/" : "https://taggbox.com/on-site-uploads-content-rights/"} className="t_tc_link ms-1" target="_blank" title={content?.termsLT ?? ""}>{content?.termsLT ?? ""}</a> *</label></div>
                        </div>
                        <div className="t_f_group t_text_center">
                            <div className="t_f_p_btn ">{btnText ? btnText : 'Submit'}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div> : null
}


export default Preview